import * as React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"


// markup
const NotFoundPage = () => {
  return (
    <Layout>
      <section className="antialiased text-gray-900 py-2 h-full flex items-center justify-center">
        <div className="md:w-2/3 mx-auto">
          <div className="main-title my-8 md:my-16 space-y-3 text-center">
            <h1 className="font-bold text-xl md:text-3xl lg:text-5xl"></h1>
            <h3 className="font-bold text-md md:text-xl lg:text-2xl"></h3>
            <h2 className="font-bold text-2xl">Mission Control, we've hit a 404.</h2>
          </div>
          <div className="my-8 md:my-16 space-y-3">
            <h2 className="text-lg text-grey-600 text-center py-3">[ AD]</h2>
            <div className="flex flex-col space-y-4">
              {/*  - <a> AD here - this an <a> wrapped card */}
              <div class="max-w-3xl mx-auto rounded-lg overflow-hidden shadow-lg my-2 bg-white dark:bg-grey-600">
                <a href="https://coscup.org/2021/zh-TW/landing" target="_blank" rel="noopener noreferrer">
                  {/*  Hero image here */}
                  <img className="w-full overflow-hidden" src="https://coscup.org/2021/assets/og.65c2d8e3.png" alt="COSCUP 2021" />
                  <div class="px-6 pt-4 space-y-3">
                    {/*  Descripion here */}
                    <h3 className="font-bold text-xl md:text-2xl lg:text-3xl">COSCUP x RubyConf Taiwan 2021 is here!</h3>
                    <p className="text-base md:text-lg lg:text-xl"> COSCUP  16  RubyConf Taiwan  7/31~8/1  22 </p>
                  </div>
                  <div class="px-6 py-4 space-y-3">
                    {/*  Hashtag here */}
                    <span class="inline-block rounded-full px-3 py-1 text-sm font-semibold bg-fern-green-50 mr-2">#COSCUP</span>
                    <span class="inline-block rounded-full px-3 py-1 text-sm font-semibold bg-fern-green-50 mr-2">#</span>
                    <span class="inline-block rounded-full px-3 py-1 text-sm font-semibold bg-fern-green-50 mr-2">#</span>
                    <span class="inline-block rounded-full px-3 py-1 text-sm font-semibold bg-fern-green-50 mr-2">#</span>
                  </div>
                </a>
              </div>
              {/*  Card can be added as needed below */}
            </div>
            <p className="block pt-16 font-bold text-center underline">
              <Link to="/"> Back to Home</Link>
            </p>
          </div>
        </div>
      </section>
    </Layout>
  )
}

export default NotFoundPage
